  color: currentColor;
  cursor: pointer;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

ul, li {
  color: currentColor;
  cursor: inherit;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  cursor: inherit;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

a {
  text-decoration: none;
  transition-property: fill, color, background-color, transform, border-color, letter-spacing;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

body {
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

button {
  transition-property: color, background-color, box-shadow, fill, transform;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

button:hover > svg {
  fill: #b92f2c;
  transform: scale(1.1);
}

button > svg {
  transition: fill .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
}

input, textarea {
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
}

input:hover, input:focus, textarea:hover, textarea:focus {
  border-color: #b92f2c;
  outline: none;
  box-shadow: 1px 0 16px rgba(227, 220, 16, .35);
}

