.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 3;
  opacity: 1;
  background-color: rgba(129, 124, 124, .3);
  transition: opacity .39s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  transition: opacity .39s cubic-bezier(.4, 0, .2, 1);
}

.backdrop.is-hidden .film-info {
  opacity: 0;
  transition: transform .35s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  transform: translate(-50%, -50%);
}

.film-info {
  opacity: 1;
  transition: transform .35s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  transform: translate(-50%, -50%);
}

.arrow-link {
  z-index: 2;
  position: fixed;
  bottom: 25px;
  right: 10px;
}

.arrow-link > svg {
  transition: fill .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
}

.arrow-link:hover > svg, .arrow-link:focus > svg {
