.slider {
  cursor: pointer;
  background-color: #ccc;
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 26px;
  width: 26px;
  background-color: #fff;
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 4px;
  left: 4px;
}

input.checked + .slider {
  background-color: #262525;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

