@charset 'utf-8';
.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  height: 40px;
  margin-bottom: 60px;
  line-height: normal;
  text-align: center;
  font-size: 0;
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 40px;
  padding: 12px;
  margin-left: 0;
  margin-right: 5px;
  color: #333;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  text-decoration: none;
  vertical-align: middle;
  border-radius: 5px;
  background-color: #f7f7f7;
  //   border: 1px solid #ddd;
  //   border-width: 1px 0;
}

.tui-pagination .tui-page-btn:hover {
  background-color: #b92f2c;
  border-radius: 5px;
  color: #fff;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  background: #b92f2c;
  border-color: #4b96e6;
  cursor: default;
}

.tui-pagination .tui-is-selected:hover {
  background-color: #4b96e6;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  height: 40px;
  padding: 0;
  font-size: 0;
  line-height: 26px;
  white-space: nowrap;
  font-weight: normal;
  //   border: 1px solid #ddd;
}

.tui-last-child {
  display: inline-flex !important;
  justify-content: center;
}

.tui-prev-is-ellip,
.tui-next-is-ellip {
  display: none !important;
}
.tui-pagination .tui-prev-is-ellip {
  //   border-right: 0;
}

.tui-pagination .tui-next-is-ellip {
  //   border-left: 0;
}

.tui-pagination .tui-first + .tui-prev,
.tui-pagination .tui-next + .tui-last {
  border-left: 0;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: bottom;
  font-size: 0px;
}

.tui-pagination .tui-ico-next {
  background: url('../images/pagination/arrow-right.svg') no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
  position: absolute;
  transform: translate(-50%, 75%);
}

.tui-pagination .tui-ico-prev {
  background: url('../images/pagination/arrow-left.svg') no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
  position: absolute;
  transform: translate(-50%, 75%);
}

.tui-page-btn .tui-prev {
  padding: 12px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}

.tui-pagination .tui-prev {
  margin-right: 15px;
}

.tui-pagination .tui-next {
  margin-left: 10px;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 11px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
  width: 16px;
  height: 16px;
  //   background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 16px;
  height: 16px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 11px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 18px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled:hover {
  cursor: default;
}

// .tui-pagination .tui-is-disabled .tui-ico-prev {
//   background-position: -26px -12px;
// }

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}

.tui-page-inputs .tui-page-current {
  color: #4b96e6;
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}
